import * as yup from "yup";

const rateValidationSchema = yup.object().shape({
    tariff: yup
        .string()
        .required("–ü–æ–ª–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º")
        .when("$rates", (rates, schema) => {
            return schema.test(
                "tariff",
                "–î–∞–Ω–Ω—ã–π —Ç–∞—Ä–∏—Ñ —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç",
                (value) => value && !rates.data.map((item) => item.rateTypeId.name).includes(value),
            );
        }),

    unit: yup.string().required("–ü–æ–ª–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º"),
    tariffPrice: yup.number().required("–ü–æ–ª–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º").typeError("–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –≤–≤–µ–¥–∏—Ç–µ —á–∏—Å–ª–æ"),
});

export default rateValidationSchema;
