import * as yup from "yup";

const pointValidationSchema = yup.object().shape({
    pointAddress: yup
        .string()
        .required("–ü–æ–ª–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º")
        .when("$points", (points, schema) => {
            return schema.test(
                "pointAddress",
                "–î–∞–Ω–Ω—ã–π –∞–¥—Ä–µ—Å —É–∂–µ —Å—É—â–µ—Å—Ç–≤—É–µ—Ç",
                (value) => value && !points.data.map((item) => item.address).includes(value),
            );
        }),

    pointName: yup.string().required("–ü–æ–ª–µ –Ω–µ –¥–æ–ª–∂–Ω–æ –±—ã—Ç—å –ø—É—Å—Ç—ã–º"),
});

export default pointValidationSchema;
