import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { useLocation, useHistory } from "react-router";
import { useDispatch, useSelector } from "react-redux";
import { apiData } from "../../../store/selectors/selectors";
import { resetError } from "../../../store/slices/apiSlice";
import { resetAuthState } from "../../../store/slices/authSlice";
import styles from "./ErrorPage.module.scss";
import Button from "../../Common/UI/Button.jsx";

const ErrorPage = () => {
    const { error } = useSelector(apiData);
    const [errorStatus, setErrorStatus] = useState({ code: "", message: "", title: "" });
    const dispatch = useDispatch();

    const location = useLocation();
    const { push, goBack } = useHistory();

    const handleClick = () => {
        dispatch(resetError());
        dispatch(resetAuthState());
        goBack();
    };

    useEffect(() => {
        if (error !== "") {
            switch (error) {
                case "Network Error":
                    setErrorStatus({
                        code: 502,
                        message: "–ü—Ä–æ–∏–∑–æ—à–ª–∞ –æ—à–∏–±–∫–∞ –ø—Ä–∏ –ø–æ–ª—É—á–µ–Ω–∏–∏ –¥–∞–Ω–Ω—ã—Ö —Å —Å–µ—Ä–≤–µ—Ä–∞. –ü–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–µ—Ä–µ–∑–∞–≥—Ä—É–∑–∏—Ç—å —Å—Ç—Ä–∞–Ω–∏—Ü—É",
                        title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(",
                    });
                    break;

                case "Unauthorized":
                    setErrorStatus({
                        code: 401,
                        message: "–û—à–∏–±–∫–∞ –ø—Ä–∏ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è",
                        title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(",
                    });
                    break;
                case "Request failed with status code 401":
                    setErrorStatus({
                        code: 401,
                        message: "–û—à–∏–±–∫–∞ –ø—Ä–∏ –∞–≤—Ç–æ—Ä–∏–∑–∞—Ü–∏–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è",
                        title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(",
                    });
                    localStorage.removeItem("access_token");
                    localStorage.removeItem("token");

                    break;
                case "Request failed with status code 404":
                    setErrorStatus({
                        code: 404,
                        message: "–£–∫–∞–∑–∞–Ω–Ω—ã–π –ø—É—Ç—å –Ω–µ –Ω–∞–π–¥–µ–Ω –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ",
                        title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(",
                    });
                    break;

                case "Cannot read property 'table' of undefined":
                    setErrorStatus({
                        code: 500,
                        message: "–£–∫–∞–∑–∞–Ω–∞–∞—è –±–∞–∑–∞ –¥–∞–Ω–Ω—ã—Ö –Ω–µ –Ω–∞–π–¥–µ–Ω–∞ –Ω–∞ —Å–µ—Ä–≤–µ—Ä–µ",
                        title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(",
                    });
                    break;
                default:
                    setErrorStatus({ code: 404, message: "–°—Ç—Ä–∞–Ω–∏—Ü–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞", title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(" });
            }
        } else {
            setErrorStatus({ code: 404, message: "–°—Ç—Ä–∞–Ω–∏—Ü–∞ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞", title: "–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫ :(" });
        }
    }, [error]);

    return (
        errorStatus.code && (
            <section className={styles.errorPage}>
                <div className={styles.errorContainer}>
                    <div className={styles.errorMessageContainer}>
                        <h2 className={styles.code}>{errorStatus.code}</h2>
                        <h1 className={styles.title}>{errorStatus.title}</h1>
                        <p className={styles.message}>{errorStatus.message}</p>
                    </div>
                    <Button name="–ù–∞–∑–∞–¥" onClick={handleClick} className={styles.button} />
                </div>
            </section>
        )
    );
};

export default ErrorPage;
